import Vue from "vue";
import VueRouter from "vue-router";
import Login from "@/components/pages/Login";
import Dashboard from "@/components/Dashboard";
import Products from "@/components/pages/Products";
import orders from "@/components/pages/orders";
import coupons from "@/components/pages/coupons";

import Shappingboard from "@/components/Shappingboard";
import Shappingfooter from "@/components/Shappingfooter";
import ShappingCart from "@/components/pages/ShappingCart";
import ShappingProducts from "@/components/pages/ShappingProducts";
import Checkout from "@/components/pages/Checkout";
import PayOrder from "@/components/pages/PayOrder";

Vue.use(VueRouter);

export default new VueRouter({
  routes: [
    {
      path: "*",
      redirect: "login"
    },
    {
      name: "login", //
      path: "/login", //
      component: Login //
    },
    {
      name: "Dashboard", //
      path: "/admin", //
      component: Dashboard, //
      children: [
        {
          name: "products", //
          path: "products", //
          component: Products, //
          meta: { requiresAuth: true }
        },
        {
          name: "orders", //
          path: "orders", //
          component: orders, //
          meta: { requiresAuth: true }
        },
        {
          name: "coupons", //
          path: "coupons", //
          component: coupons, //
          meta: { requiresAuth: true }
        }
      ]
    },
    {
      name: "Shappingboard", //
      path: "/", //
      // component: Shappingboard, //
      components: {
        default: Shappingboard,
        footer: Shappingfooter
      },
      children: [
        {
          name: "ShappingCart", //
          path: "shappingcart", //
          component: ShappingCart //
        },
        {
          name: "ShappingProducts", //
          path: "shappingproducts/:id", //
          component: ShappingProducts //
        },
        {
          name: "Checkout", //
          path: "checkout", //
          component: Checkout //
        },
        {
          name: "PayOrder", //
          path: "payorder/:orderId", //
          component: PayOrder //
        }
      ]
    }
  ]
});



